@import "./shared.scss";
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 14px;
    background: #010836;
    color: #fff;
}

input,
button,
textarea {
    &:focus {
        outline: 0;
    }
}

ul {
    list-style: none;
}

body, html, #root{
    height: 100%;
}


#root {
    height: 100%;
    max-width: 1125px;
    margin: 0 auto;
}

.clearFloat{
    zoom:1;

    &:after{
        display: block;
        clear: both;
        content: " ";
        visibility: hidden;
        height: 0;/*其他适用*/
    }
}

.wrapper{
    width: 100%;
    height: 100%;
    background: url('../images/bg.jpg') no-repeat #010837 fixed;
    background-size: 100%;
    display: flex;
    flex-direction: column;
    overflow: hidden;

    .main{
        flex-grow: 1;
        overflow-y: auto;
    }

    .footer{
        flex-shrink: 0;
        height: px(170);
    }

    .nav-bar{
        height: px(170);
        //padding-bottom: px(66);
        width: 100%;
        position: fixed;
        bottom: 0;
        background: #13082d;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        z-index: 100;
        max-width: 1125px;

        a{
            display: block;
            height: px(140);
            line-height: px(140);
            padding: 0 px(20);
            color: #fff;
            text-decoration: none;
            font-size: px(40);
        }

        i{
            display: block;
            height: px(42);
            width: 1px;
            background: #999;
        }
    }
}

.c-btn{
    width: px(372);
    height: px(90);
    font-size: px(50);
    color: #fff;
    text-align: center;
    //line-height: px(90);
    padding: 0 px(50);
    box-sizing: border-box;
    background-color: #4d2b8e;
    background-image: $linear-gradient;
    border: 0;
    border-radius: px(18);
}
