@import "../../css/shared.scss";

.c-modal{

  &-mask{
    width: 100%;
    height: 110%;
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(0,0,0,.6);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10000;
  }

  &-content{
    width: 70%;
    height: auto;
    border-radius: px(40);
    color: #333;
    overflow: hidden;
  }

  &-body{
    padding: px(60) px(40);
    font-size: px(40);
    line-height: 1.5;
    color: #444;
    background: #fff;
  }

  &-footer{
    //border-top: 1px solid #ddd;
    text-align: center;
    padding: px(40);
    font-size: px(46);
    background-image: $linear-gradient;
    color: #fff;
  }
}
